#home-main-slider{
    margin-bottom: $mb-30;
    .main-slider{
        &__item{
            position: relative;
            img{
                width: 100%;
            }
            .collection-info{
                width: 40%;
            }
        }
    }
    .owl-carousel{
        .owl-dots{
            display: flex;
            position: absolute;
            bottom: 10px;
            left: 50%;
            transform: translateX(-50%);
            .owl-dot{
                &{
                    margin: 5px 7px;
                }
                @include transition-ease;
                span{
                    display: block; 
                    transition: all .4s ease;  
                    width: 16px;
                    height: 16px;
                    margin: 0px;
                    border: 1px solid $c-white;
                    background-color: transparent;
                    @include transition-ease;
                    &:hover{
                        background-color: $c-white !important;
                    }
                }
                &.active{
                    span{
                        background-color: $c-white; /* Active and on hover color */
                    }
                }
                &:focus{
                    border: none;
                    outline: 0;           
                }
            }
        }
        .owl-nav{
            display: none;
        }
    }
}

/* TABLET */
@include max-tablet{
    #home-main-slider{
        margin-bottom: $mb-20;
        .owl-carousel{
            .owl-dots{
                bottom: 10px;
                .owl-dot{
                    &{
                        margin: 5px 5px;
                    }
                    span{
                        width: 12px;
                        height: 12px;
                        // &:hover{
                        //     background-color: initial; /* Active and on hover color */
                        // }
                    }
                }
            }
        }
    }
}

/* MOBILE */
@include max-mobile{
    #home-main-slider{
        margin-bottom: $mb-15;
        .main-slider{
            &__item{
                > a{
                    margin-bottom: $mb-15;
                }
                .collection-info{
                    position: static;
                    top: initial;
                    right: initial;
                    transform: translateX(0%);
                    width: 100%;
                    text-align: center;
                    padding: 0px 10px;
                    .title{
                        margin-bottom: $mb-5;
                        a{
                            text-transform: uppercase;
                            font-size: $font-size-primary;
                            font-weight: bold;
                            color: $c-black;
                        }
                    }
                    .desc{
                        display: block;
                        color:  $c-black;
                        font-size: $fs-12;
                        line-height: $lh-16;
                        margin-bottom: $mb-20;
                    }
                    .btn-shop-now{
                        padding: 0px;
                        a{
                            font-size: $fs-12;
                            border: 1px solid $c-black;
                            color: $c-black;
                            @include transition-ease;
                            &:hover{
                                background-color: $c-black;
                                color: $c-white;
                            }
                            &:focus{
                                outline: 0;
                            }
                        }     
                    }
                }
            }
        }
        .owl-carousel{
            .owl-dots{
                display: none;
            }
        }
    }
}