.product-wrapper {
    margin: 30px 0px;
    .left {
        padding: 0px;
        img {
            cursor: pointer;
        }

        [class*="col-"] {
            padding-left: 10px;
            padding-right: 10px;
        }

        .slider-nav {
            img {
                margin-bottom: 5px;
            }
            .slick-active.slick-current {
                border: 1px solid $c-grey-8b8b8b;
            }
        }

        .slider-for {
            display: flex;
            flex-direction: column;
        }

        .slick-slider {
            .slick-slide {
                &:focus {
                    outline: none;
                }
            }

            .prev-arrow,
            .next-arrow {
                position: absolute;
                top: 50%;
                font-size: 30px;
                opacity: 0.7;
                cursor: pointer;
                color: $c-black-181818;
                span {
                    top: 50%;
                    transform: translateY(-50%);
                    cursor: pointer;
                    font-size: 30px;
                    opacity: 0.5;
                    @include transition-ease;
                    &:hover {
                        opacity: 1;
                    }
                }
            }
            .prev-arrow {
                left: 15px;
                z-index: 99;
            }
            .next-arrow {
                right: 15px;
                z-index: 99;
            }
        }
    }
    .right {
        padding: 0px 40px;
        .item-name {
            font-family: "Alegreya", serif !important;
            font-size: 30px;
            font-weight: 300;
            letter-spacing: 3px;
            text-align: left;
            margin-bottom: $mb-15;
        }
        .visually-hidden {
            display: none;
        }
        .product-price {
            font-size: 22px;
            font-weight: 300;
            margin-bottom: $mb-15;
        }
        .product-rating {
            @include flexCenter(row);
            flex-wrap: wrap;
            justify-content: flex-start;
            span,
            a {
                display: inline-block;
            }
            a {
                margin-left: 3px;
            }
            > div,
            > span {
                margin: 0px 10px 10px 0px;
                font-size: 12px;
            }
            .product-sku {
                font-size: 12px;
            }
            .pro-brand {
                display: flex;
            }
        }
        #product-single-details {
            padding: 10px 0px 5px;
            border-top: 1px solid $c-white-e5e5e5;
            margin-bottom: 20px;
            border-bottom: 1px solid $c-white-e5e5e5;
            form {
                margin-bottom: $mb-15;
                [id*="variant-swatch-"],
                .quantity-addcart {
                    display: flex;
                    align-items: center;
                    padding: 10px 0px;
                    input[type="radio"] {
                        display: none;
                    }
                    label {
                        border: 1px solid #ccc;
                        padding: 0 10px;
                        min-width: 35px;
                        height: 35px;
                        margin: 0;
                        text-align: center;
                        line-height: 35px;
                        white-space: nowrap;
                        font-size: 12px;
                        cursor: pointer;
                        &.sd {
                            border: 1px solid $c-black;
                        }
                    }
                    span {
                        display: block;
                        min-width: 60px;
                    }
                    .select-swap {
                        @include flexCenter(row);
                        justify-content: flex-start;
                        flex-wrap: wrap;
                        label {
                            margin-bottom: 0px;
                        }
                        > div {
                            @include flexCenter(row);
                            position: relative;
                            margin-bottom: 5px;
                            .img-check,
                            .crossed-out {
                                display: none;
                                position: absolute;
                            }
                            .img-check {
                                bottom: 0;
                                right: 0;
                                &.block {
                                    display: block;
                                }
                            }
                            .crossed-out {
                                top: 0;
                                left: 0;
                                width: 100%;
                                height: 100%;
                                &.block {
                                    display: block;
                                }
                            }
                            justify-content: flex-start;
                            margin-right: 8px;
                        }
                    }
                }

                .quantity-addcart {
                    padding: 10px 0px 0px 0px;
                    .quantity {
                        margin-right: 10px;
                    }
                    .js-quantity {
                        position: relative;
                        width: 100%;
                        max-width: 50px;
                        min-width: 75px;
                        height: 34px;
                        .js-quantity__adjust {
                            position: absolute;
                            display: block;
                            border: none;
                            padding: 0 8px;
                            background-color: transparent;
                            overflow: hidden;
                        }
                        .minus-icon {
                            top: 0;
                            bottom: 0;
                            left: 0;
                            border-right: 1px solid $c-white-e5e5e5;
                        }
                        .plus-icon {
                            top: 0;
                            bottom: 0;
                            right: 0;
                            border-left: 1px solid $c-white-e5e5e5;
                        }
                        input {
                            max-width: 100%;
                            height: 100%;
                            padding: 5px 25px;
                            text-align: center;
                            border: 1px solid $c-white-e5e5e5;
                            border-radius: 3px;
                        }
                    }
                }

                .favorite_btn {
                    min-height: 28px;
                    margin: 16px 0px 17px 20px;
                    display: block;
                    font-size: 14px;
                    border-radius: 3px;
                    background-color: $c-black;
                    color: $c-white;
                    padding: 3px 10px;
                    white-space: nowrap;
                }

                .product-actions {
                    @include flexCenter(column);
                    align-items: flex-start;

                    button {
                        width: 100%;
                        max-width: 100%;
                    }

                    .btn-addToCart {
                        margin-bottom: $mb-15;
                    }
                }
            }
        }
        .share-social {
            margin-bottom: $mb-30;
        }
    }
}

#related-product {
    .product-item {
        position: relative;
        margin: 0px 15px;
        padding: 0px;

        .image {
            &::after {
                content: "";
                position: absolute;
                top: 5px;
                bottom: 5px;
                left: 5px;
                right: 5px;
                border-top: 1px solid $c-white;
                border-bottom: 1px solid $c-white;
                transform: scale(0, 1);
                opacity: 0;
                @include transition-ease;
            }
            &:hover::after {
                transform: scale(1);
                opacity: 1;
                z-index: 10;
            }
        }

        .product-item__info {
            position: absolute;
            width: 100%;
            padding: 15px 10px;
            margin: 0px;
            bottom: 0;
            left: 0;
            background-color: rgba(0, 0, 0, 0.5);
            color: #eee;
            text-align: center;
            z-index: 10;
            line-height: 2;
            .title {
                display: flex;
                justify-content: center;
                margin-bottom: 0px;
                a {
                    font-family: Arial, Helvetica, sans-serif;
                    font-size: $fs-14;
                    text-transform: uppercase;
                    font-weight: bold;
                    color: $c-white;
                    line-height: 20px;
                    @include transition-ease;
                    &::after {
                        content: "";
                        display: block;
                        position: absolute;
                        bottom: 2px;
                        left: initial;
                        height: 1px;
                        width: 0%;
                        background-color: $c-white;
                    }
                    &:hover::after {
                        width: 100%;
                    }
                }
            }
            .sku-loop {
                display: block;
                margin: 0px;
                font-weight: 300;
            }
            .current-price {
                display: block;
                font-size: $fs-16;
                color: $c-white;
                line-height: 20px;
                font-weight: bold;
            }
        }
    }
}

#related-product,
#seen-product {
    .owl-carousel {
        .owl-dots {
            margin: 10px 0px 0px;
            .owl-dot {
                span {
                    margin: 5px 7px;
                    border-radius: 20px;
                    background-color: #869791; /* Active and on hover color */
                }
            }
        }
        .owl-nav {
            .owl-prev {
                left: 10px;
                z-index: 99;
            }
            .owl-next {
                right: 10px;
                z-index: 99;
            }
        }
    }
}

#modalAddComplete {
    position: fixed;
    opacity: 0;
    visibility: hidden;
    width: 100%;
    top: -100%;
    left: 50%;
    margin-bottom: $mb-30;
    transform: translate(-50%, -50%);
    z-index: 999;
    width: 60%;
    max-width: 100%;
    transition: all 0.7s ease;

    &.open {
        top: 50%;
        opacity: 1;
        visibility: visible;
    }
    .modalAddComplet-content {
        position: relative;
        margin: 0px auto;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        background-color: #fff;
        border: 1px solid $c-white-e5e5e5;
        border-radius: 8px;
        > .modalAddComplete-close {
            position: absolute;
            width: 30px;
            height: 30px;
            top: -15px;
            right: -15px;
            font-size: 18px;
            background-color: $c-black;
            border-radius: 50%;
            cursor: pointer;
            @include flexCenter(row);
            i {
                color: $c-white;
            }
        }

        .modalAddComplete-body {
            .modal-cart-status {
                h2 {
                    text-transform: uppercase;
                    font-weight: bold;
                    font-size: 16px;
                    padding: 15px;
                    margin-bottom: 0px;
                }
            }
            form {
                .modal-tbl-cart {
                    margin-bottom: $mb-15;
                    max-height: 210px;
                    overflow-y: auto;
                    border-bottom: 1px solid $c-white-e5e5e5;

                    @include max-mobile {
                        margin-bottom: 0;
                    }

                    @include custom-scrollbar-black;

                    table {
                        width: 100%;
                        max-width: 100%;
                        th,
                        td {
                            padding: 5px;
                            text-align: center;
                        }

                        tr {
                            border-bottom: 1px solid $c-white-e5e5e5;
                        }

                        thead {
                            border-top: 1px solid $c-white-e5e5e5;
                            border-bottom: 1px solid $c-white-e5e5e5;
                            th {
                                text-align: center;
                                font-weight: normal;
                            }
                        }

                        tbody {
                            .product-img {
                                .product-title {
                                    text-align: left;
                                }
                            }

                            tr:last-child {
                                border-bottom: 0;
                            }

                            .product-title {
                                a {
                                    text-transform: uppercase;
                                    font-weight: bold;
                                    font-size: 12px;
                                }
                                small {
                                    font-size: 0.9em;
                                }
                            }
                            .product-price {
                                .current-price {
                                    text-transform: uppercase;
                                    font-weight: bold;
                                    font-size: 12px;
                                }
                            }
                            .product-actions {
                                text-transform: uppercase;
                                font-weight: bold;
                                font-size: 12px;
                                .js-quantity__number {
                                    max-width: 55px;
                                }
                            }
                            .product-remove {
                                .cart__remove {
                                    font-size: 18px;
                                }
                            }
                        }
                        .product-actions {
                            input::-webkit-outer-spin-button,
                            input::-webkit-inner-spin-button {
                                -webkit-appearance: none;
                                margin: 0;
                            }

                            /* Firefox */
                            input[type="number"] {
                                -moz-appearance: textfield;
                            }
                            input {
                                width: 100%;
                                max-width: 100%;
                                padding: 8px 10px;
                                border: 1px solid $c-white-e5e5e5;
                                border-radius: 3px;
                                &:focus {
                                    outline: 0;
                                }
                            }
                        }
                    }
                }

                .modal-checkout-actions {
                    padding: 15px;
                    [class*="col-"] {
                        padding: 0px;
                    }
                    .left {
                        .cart-note-wrapper {
                            .cart-note {
                                textarea {
                                    width: 100%;
                                    max-width: 100%;
                                    min-height: 100px;
                                    border: 1px solid $c-white-e5e5e5;
                                    padding: 8px 10px;
                                    overflow: auto;
                                    resize: both;
                                }
                            }
                        }
                        .modalAddComplete-close {
                            display: inline-block;
                        }
                    }
                    .right {
                        .modal-cart-checkout {
                            .modal-cart-sum {
                                h3 {
                                    font-size: 14px;
                                    font-weight: bold;
                                }
                            }
                            .modal-cart-actions {
                                button,
                                a {
                                    font-size: 12px;
                                    font-weight: bold;
                                    text-transform: uppercase;
                                    padding: 0px 15px;
                                    height: 38px;
                                    line-height: 38px;
                                    outline: 0;
                                    border: none;
                                    border-radius: 8px;
                                    @include max-mobile {
                                        font-size: 11px;
                                    }
                                }
                                button {
                                    margin-right: 5px;
                                    background: #e6e6e6;
                                    color: $c-black;
                                    @include max-smobile {
                                        margin-right: 0;
                                        margin-bottom: 10px;
                                    }
                                }
                                a {
                                    background: $c-black;
                                    color: #e6e6e6;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.table-product {
    margin: 20px 5px $mb-30 5px;
    .table-product__info {
        padding-left: 0px;
        padding-right: 12px;
        .product-detail {
            span {
                font-weight: bold;
                display: inline-block;
                background-color: $c-black;
                color: $c-white;
                border-bottom: 2px solid transparent;
                padding: 10px 15px;
                margin-right: 3px;
                margin-bottom: -1px;
            }
        }
        .product-table {
            padding: 20px;
            border: 1px solid $c-white-e5e5e5;
            line-height: 24px;
            table {
                width: 1200px;
                h3 {
                    font-weight: bold;
                    margin-bottom: 0px;
                }
                span {
                    white-space: pre-line;
                }
                h3,
                span {
                    font-size: 15pt;
                }
                table,
                td,
                tr {
                    border: 1px solid $c-white-e5e5e5;
                    vertical-align: top;
                }
                td {
                    padding: 15px;
                }
            }
        }
    }
}

@include max-desktop {
    .table-product {
        .table-product__info {
            padding-right: 0px;
            .product-table {
                padding: 15px;
                table {
                    width: auto;
                }
            }
        }
    }
}

@include max-tablet {
    .product-wrapper {
        margin-top: 0px;
        .slider-nav {
            display: none;
        }
        .grid-full {
            .right {
                padding: 0 5px;
            }
        }
    }

    #related-product {
        .product-item {
            .product-item__info {
                padding: 7px 10px;
                bottom: 0;
                line-height: 1.5;
                .title {
                    margin-bottom: 0px;
                    a {
                        font-size: $fs-13;
                    }
                }
                .sku-loop {
                    font-size: $fs-13;
                }
                .current-price {
                    font-size: $fs-13;
                    line-height: 20px;
                }
            }
        }
    }

    #related-product,
    #seen-product {
        .owl-carousel {
            .owl-dots {
                .owl-dot {
                    span {
                        margin: 5px 5px;
                        width: 10px;
                        height: 10px;
                    }
                }
            }
            .owl-prev {
                left: 0px;
            }
            .owl-next {
                right: 0px;
            }
        }
    }

    .table-product {
        display: flex;
        justify-content: center;
        .table-product__info {
            .product-table {
                padding: 10px;
                table {
                    width: auto;
                    h3,
                    span {
                        font-size: $fs-14;
                    }
                }
            }
        }
    }

    #modalAddComplete {
        width: 90%;
    }
}

@include max-mobile {
    .product-wrapper {
        .grid-full {
            .left {
                padding: 15px;
            }
            .right {
                #product-single-details {
                    form {
                        .quantity-addcart {
                            span {
                                min-width: 40px;
                            }
                        }
                    }
                }
            }
        }
    }
    .table-product {
        display: flex;
        justify-content: center;
        .table-product__info {
            .product-detail {
                span {
                    font-size: $fs-12;
                }
            }
            .product-table {
                table {
                    width: auto;
                    h3,
                    span {
                        font-size: $fs-12;
                    }
                }
            }
        }
    }
}
