.back-to-top {
    position: fixed;
    text-align: center;
    width: 40px;
    height: 40px;
    line-height: 36px;
    border-radius: 100%;
    right: 15px;
    bottom: 15px;
    background: $c-black-181818;
    border: 2px solid $c-white;
    opacity: 0;
    visibility: hidden;
    transition: 0.5s ease-in-out;
    z-index: 999999;
    i {
        font-size: 14px;
        color: $c-white;
    }
    &.open {
        opacity: 1;
        visibility: visible;
    }
}

/* Responsive */
@include max-tablet {
    .back-to-top {
        width: 56px;
        height: 56px;
        bottom: 75px;
        line-height: 60px;
        i {
            font-size: 25px;
            color: $c-white;
        }
    }
}
