.auth {
    margin: 120px 0px;

    #CustomerForgetPasswordForm {
        display: none;
        &.open {
            display: block;
        }
    }

    #CustomerLoginForm {
        &.close {
            display: none;
        }
    }

    .auth-form {
        @include flexCenter(column);

        .section-title {
            margin-bottom: 12px;
            font-weight: bold;
            letter-spacing: 0px;
        }

        &__body {
            @include flexCenter(column);
            width: 100%;

            > * {
                margin-bottom: 15px;
            }

            input {
                display: block;
                width: 100%;
                max-width: 100%;
                padding: 8px 10px;
                border-radius: 3px;
                outline: none;
                border: 1px solid $c-white-e5e5e5;
                &:focus {
                    border: 1px solid $c-grey-8b8b8b;
                }
            }

            button {
                width: 100%;
                padding: 8px 10px;
                margin-bottom: 15px;
                font-weight: bold;
                line-height: 1.42;
                border-radius: 2px;
                border: 1px solid $c-black;
                color: $c-white;
                background-color: $c-black;
                @include transition-ease;
                &:hover {
                    background-color: $c-white;
                    color: $c-black;
                }
                &:focus {
                    outline: 0;
                }
            }
        }

        &__footer {
            text-align: center;

            > * {
                margin-bottom: 10px;
            }
        }
    }
}

#RecoverPasswordForm {
    display: none;
    .section-title {
        letter-spacing: 0px;
        font-size: 2em;
    }
    .top {
        * {
            margin-bottom: 15px;
        }
    }
}

// TABLET
@include max-tablet {
    .auth {
        margin: $mb-20 0px;
        form {
            .section-title {
                font-size: 20px;
            }
            .body {
                font-size: $fs-13;
            }
            .bottom {
                font-size: $fs-13;
            }
        }
    }
}

// MOBILE
@include max-mobile {
    .auth {
        margin: $mb-15 0px;
        form {
            .section-title {
                font-size: 20px;
            }
            .body {
                font-size: $fs-12;
            }
            .bottom {
                font-size: $fs-12;
            }
        }
    }
}
