#home-banner1{
    margin-bottom: $mb-30;
    .home-collection-wrapper{
        position: relative;
        .collection-info{
            width: 40%;
        }
    }
}

/* TABLET */
@include max-tablet{
    #home-banner1{
        margin-bottom: $mb-20;
        .home-collection-wrapper{
            .collection-info{
                top: calc(50% - 120px);
                padding: 0px 15px;
                .desc{
                    display: block;
                }
            }
        }
    }
}

/* MOBILE */
@include max-mobile{
    #home-banner1{
        margin-bottom: $mb-15;
        .home-collection-wrapper{
            .collection-info{
                top: 50%;
                width: 100%;
                .desc{
                    display: none;
                }
            }
        }
    }
}
