#contact {
    .section-title {
        margin-bottom: 12px;
        font-weight: bold;
        letter-spacing: 0px;
    }
    .contact-map {
        /* Responsive for bitmap */
        .iframe-rwd {
            position: relative;
            padding-bottom: 540px;
            padding-top: 10px;
            height: 0;
            overflow: hidden;
        }
        .iframe-rwd iframe,
        .iframe-rwd object,
        .iframe-rwd embed {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }
    .list-contact-item {
        .contact-item {
            @include flexCenter(row);
            justify-content: flex-start;
            margin-bottom: $mb-20;
        }
        .contact-info {
            margin-left: 10px;
        }
        i {
            font-size: 19px;
        }
    }
    .contact-form {
        form {
            @include flexCenter(column);
            align-items: flex-start;
            input,
            textarea,
            button {
                border: 1px solid $c-white-e5e5e5;
                border-radius: 0px;
                margin: 10px 10px 30px 0px;
                outline: none;
                border-radius: 2px;
                &:focus {
                    border: 1px solid $c-grey-8b8b8b;
                }
            }
            > div {
                @include flexCenter(column);
                align-items: flex-start;
                width: 100%;
            }
            textarea {
                margin-bottom: 10px;
            }
            button {
                margin-top: 10px;
                width: auto;
                border: 1px solid $c-black;
                &:focus {
                    border: 1px solid $c-black;
                }
            }
        }
    }
}

@include max-tablet {
    #contact {
        .left-col {
            font-size: $fs-13;
            margin-bottom: $mb-20;
        }
        .right-col {
            font-size: $fs-12;
            .contact-form {
                form {
                    button {
                        margin-bottom: 5px;
                    }
                }
            }
        }
    }
}

@include max-mobile {
    margin: $mb-15 0px;
    #contact {
        .left-col {
            font-size: $fs-12;
            margin-bottom: $mb-20;
        }
        .right-col {
            font-size: $fs-12;
        }
    }
}
