.collection-wrapper {
    margin: $mb-15 0px $mb-30 0px;
    &__header {
        @include flexCenter(row);
        justify-content: space-between;
        flex-wrap: wrap;
        padding: 5px 20px;
        > div {
            flex: 0 0 33.33%;
            max-width: 33.33%;
        }
        .filter {
            padding: 11px 5px;
            cursor: pointer;
        }
        .section-title {
            margin-bottom: 0px;
            font-weight: bold;
            letter-spacing: 0px;
        }
        .form {
            label {
                margin-bottom: 0px;
                margin-right: 5px;
            }
            select {
                border: 1px solid #e5e5e5;
                max-width: 100%;
                padding: 8px 10px;
                border-radius: 3px;
            }
            @include flexCenter(row);
            justify-content: flex-end;
        }
    }
    .collection-content {
        position: relative;
        overflow: hidden;
        .collection-list {
            @include transition-ease;
            &.marginLeft {
                transform: translateX(33.33%);
            }
            .pagination {
                margin: 30px 0px 14px 0px;
                .pagination-custom-list {
                    margin: 0px auto;
                    @include flexCenter(row);
                    .page {
                        display: flex;
                        align-items: center;
                        cursor: pointer;
                        text-align: center;
                        display: block;
                        color: $c-black;
                        background-color: $c-white;
                        width: 40px;
                        height: 40px;
                        line-height: 40px;
                        border: 1px solid;
                        margin: 0px 3px;
                        @include transition-ease;
                        &:hover {
                            a {
                                color: $c-white;
                            }
                            background-color: $c-black;
                            border: 1px solid $c-black;
                        }
                        &.active {
                            a {
                                color: $c-white;
                            }
                            background-color: $c-black;
                            border: 1px solid $c-black;
                        }
                        i {
                            font-size: $fs-12;
                            @include max-mobile {
                                font-size: 10px;
                            }
                        }
                    }
                }
            }
        }

        .collection-sidebar {
            position: absolute;
            width: 33.33%;
            left: -100%;
            bottom: 0px;
            height: 100%;
            opacity: 0;
            visibility: hidden;
            background-color: $c-white;
            z-index: 95;
            @include transition-ease;

            &.open {
                left: 0%;
                opacity: 1;
                visibility: visible;
                overflow-x: hidden;
            }

            &__header {
                @include flexCenter(row);
                justify-content: space-between;
                padding: 20px;
                border-bottom: 1px solid $c-white-e5e5e5;
                background-color: $c-white-f6f6f6;
                .close-sideBar {
                    cursor: pointer;
                }
            }
            &__body {
                padding: 0px 20px;

                .filter-colour__body {
                    padding: 0px;
                    max-height: 185px;
                }

                li {
                    .top {
                        cursor: pointer;
                        @include flexCenter(row);
                        justify-content: space-between;
                        padding: 10px 0px;
                        span {
                            font-weight: bold;
                        }
                    }

                    .body {
                        display: none;
                    }
                }

                label {
                    @include flexCenter(row);
                    justify-content: flex-start;
                    margin-bottom: 5px;
                    input[type="checkbox"],
                    input[type="radio"] {
                        width: 16px;
                        height: 16px;
                        margin-right: 8px;
                        position: relative;
                        &::after {
                            content: "";
                            position: absolute;
                            min-width: 16px;
                            min-height: 16px;
                            background-color: $c-white;
                            border: 1px solid $c-white-c3c3c3;
                            border-radius: 3px;
                        }
                        &:checked::after {
                            content: "\2714";
                            max-width: 100%;
                            max-height: 100%;
                            border: 1px solid $c-white-c3c3c3;
                            line-height: 16px;
                            color: $c-black;
                            text-align: center;
                            outline: none;
                        }
                    }
                }
            }
        }
    }
}
@include max-tablet {
    .collection-wrapper {
        margin-bottom: $mb-20;
        > div {
            font-size: $fs-13;
        }
        .collection-content {
            .collection-list {
                .pagination {
                    margin-top: $mb-20;
                    font-size: $fs-13;
                    .pagination-custom-list {
                        .page {
                            width: 32px;
                            height: 32px;
                            line-height: 30px;
                        }
                    }
                }
            }
            .collection-sidebar {
                &__header {
                    font-size: $fs-13;
                }
                &__body {
                    font-size: $fs-13;
                    li {
                        .top {
                            span {
                                font-size: $fs-14;
                            }
                        }
                    }
                }
            }
        }
    }
}

@include max-mobile {
    .collection-wrapper {
        margin-bottom: $mb-15;
        > div {
            font-size: $fs-12;
        }
        .filter {
            padding: 11px 0px;
        }
        &__header {
            > div {
                flex: 0 0 50%;
                max-width: 50%;
            }
            .title {
                flex: 0 0 100% !important;
                max-width: 100% !important;
            }
        }
        .collection-content {
            .collection-list {
                &.marginLeft {
                    transform: translateX(100%);
                }
                .pagination {
                    margin-top: $mb-15;
                    font-size: $fs-12;
                }
            }
            .collection-sidebar {
                width: 100%;
                &__header {
                    font-size: $fs-12;
                }
                &__body {
                    font-size: $fs-12;
                }
            }
        }
    }
}

@include max-smobile {
    .collection-wrapper {
        &__header {
            > div {
                flex: 0 0 100%;
                max-width: 100%;
            }
            .section-title {
                flex: 0 0 100%;
                max-width: 100%;
            }
        }
    }
}
