#system-store {
    .title {
        font-size: 24px;
        font-weight: bold;
        margin-bottom: 22px;
    }

    .store {
        margin-bottom: $mb-30;
        .image {
            margin-bottom: 5px;
            overflow: hidden;
            img {
                width: 100%;
                @include transition-ease;
            }
            &:hover img {
                transform: scale(1.05);
            }
        }
        i {
            font-size: 12px;
        }
    }
}
