footer {
    background-color: $c-black-111111;
    color: $c-grey-8b8b8b;
    box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.3);

    .p-20 {
        padding: 20px;
    }

    .ft-title {
        font-size: $fs-14;
        text-transform: uppercase;
        font-weight: bold;
    }

    a {
        color: $c-grey-8b8b8b;
        &:hover {
            color: $c-grey-8b8b8b;
        }
        &:focus {
            color: $c-grey-8b8b8b;
        }
    }

    .grid-left {
        display: flex;

        i,
        span,
        a {
            font-size: $fs-13;
        }

        .grid-item {
            .footer-logo {
                img {
                    width: 80px;
                }
            }
            .bct-logo {
                img {
                    width: 200px;
                }
            }
            .footer-logo {
                margin-bottom: 20px;
            }
            .ft-list {
                &.hover-underline {
                    a::after {
                        background-color: white;
                    }
                }

                .ft-item {
                    display: flex;
                    margin-bottom: 0.25em;
                    a {
                        font-weight: 300;
                        margin: 5px 0px;
                    }
                }
            }
            .ft-address,
            .ft-hotline,
            .ft-email,
            .bct-logo {
                display: flex;
                margin-bottom: 10px;
                i {
                    margin-right: 10px;
                }
            }
        }
        .col-40-per {
            flex: 0 0 40%;
            max-width: 40%;
        }
        .col-30-per {
            flex: 0 0 30%;
            max-width: 30%;
        }
    }

    .grid-right {
        text-align: center;
        h2 {
            margin-bottom: $mb-30;
        }
        .social-list {
            display: inline-flex;
            justify-content: center;
            border-bottom: 1px solid $c-grey-8b8b8b;
            margin-bottom: $mb-30;
            .social-icon {
                padding-bottom: 20px;
                a {
                    font-size: 36px;
                }
                i {
                    @include transition-ease;
                    &:hover {
                        color: rgba(139, 139, 139, 0.5);
                    }
                }
                &.facebook {
                    margin-right: 10px;
                }
            }
        }
    }

    .footer-subscribe {
        h4 {
            margin-bottom: $mb-30;
        }
        form {
            display: flex;
            border-radius: 3px;
            overflow: hidden;
            input,
            button {
                outline: none;
                border: none;
            }

            input {
                width: 100%;
                max-width: 100%;
                padding: 8px 10px;
                height: 37px;
            }

            button {
                white-space: nowrap;
                background-color: $c-black;
                color: white;
                padding: 0px 12px;
            }
        }
    }

    .copyright {
        text-align: center;
        span {
            display: block;
            padding: 10px 0px;
        }
    }
}

/* TABLET */
@include max-mobile {
    footer {
        .ft-title {
            font-size: $fs-13;
        }
        .grid-left {
            i,
            span,
            a {
                font-size: $fs-12;
            }
            .grid-item {
                margin-bottom: $mb-25;
            }
        }
        .copyright {
            span {
                font-size: $fs-12;
            }
        }
    }
}

/* SMOBILE */
@include max-smobile {
    footer {
        .grid-left {
            flex-wrap: wrap;
            .grid-item {
                flex: 0 0 100%;
                max-width: 100%;
            }
        }
        .copyright {
            span {
                padding-bottom: 75px;
            }
        }
    }
}
