#home-collection-list{
    .home-collection{
        max-width: 1200px;
        .home-collection-item{
            margin-bottom: $mb-30;
            img{
                width: 100%;
                cursor: pointer;
                @include transition-ease;
            }
            .image{
                position: relative;
                overflow: hidden;
                > a{
                    &:before{
                        position: absolute;
                        content: '';
                        top: 0;
                        bottom: 0;
                        left: 0px;
                        right: 0px;
                        background-color: rgba(0, 0, 0, 0.3);
                        z-index: 10;
                    }
                    &:hover img{
                        transform: scale(1.02);
                    }
                }

                .collection-info{
                    top: initial;
                    right: initial;
                    bottom: 15%;
                    left: 50%;
                    transform: translateX(-50%);
                    text-align: center;
                    z-index: 20;
                    .title{
                        a{
                            font-size: $font-size-other;
                        }
                        margin-bottom: $mb-30;
                    }
                    .btn-white-more{
                        display: inline-block;
                    }
                }
            }
        }
    }
}

/* TABLET */
@include max-tablet{
    #home-collection-list{
        .home-collection{
            .home-collection-item{
                margin-bottom: $mb-20;
                .image{
                    .collection-info{
                        .title{
                            margin-bottom: $mb-20;
                            a{
                                font-size: $font-size-other;
                            }
                        }
                        width: 100%;
                        padding: 0px 15px;
                        .btn-white-more{
                            margin-top: 10px;
                        }
                    }
                }
            }
        }
    }
}

/* MOBILE */
@include max-tablet{
    #home-collection-list{
        .home-collection{
            .home-collection-item{
                margin-bottom: $mb-15;
                .image{
                    .collection-info{
                        .title{
                            margin-bottom: $mb-20;
                            a{
                                font-size: $font-size-other;
                            }
                        }
                        width: 100%;
                        padding: 0px 15px;
                        .btn-white-more{
                            margin-top: 10px;
                        }
                    }
                }
            }
        }
    }
}