/* === BEGIN COMMON SCSS === */
* {
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}

body {
    position: relative;
    font-family: "Times New Roman", Times, serif;
    background-color: $background-primary;
    font-size: $font-size-primary;
    line-height: $line-height-primary;
}

ul {
    list-style-type: none;
    margin-bottom: 0px;
}

a {
    display: block;
    color: $c-black;
    text-decoration: none !important;
    &:hover {
        color: $c-black;
    }
    &:focus {
        color: $c-black;
    }
}

input,
textarea {
    line-height: 1.6;
    width: 100%;
    max-width: 100%;
    padding: 8px 10px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: 0px;
}

img {
    width: auto;
    max-width: 100%;
}

button {
    background-color: transparent;
    border: none;
    &:focus {
        outline: none;
    }
}

.limit-one-line {
    .title {
        a {
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
        }
    }
}

.container {
    @include smobile {
        max-width: 560px;
    }
    @include mobile {
        max-width: 750px;
    }
    @include tablet {
        max-width: 980px;
    }
    @include desktop {
        max-width: 1200px;
    }
}

.wrapper {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 30px;
}

.section-title {
    font-size: $font-size-other;
    letter-spacing: 2px;
    text-align: center;
    margin-bottom: 12px;
}

.hover-underline {
    a {
        display: inline-block;
        position: relative;
        &::after {
            position: absolute;
            content: "";
            display: block;
            left: 0;
            bottom: -2px;
            height: 1px;
            width: 0px;
            background-color: $c-black-181818;
            overflow: hidden;
            @include transition-ease;
        }
        &:hover::after {
            width: 100%;
        }
    }
}

.btn-white-more {
    a {
        display: inline-block;
        padding: 7px 15px;
        border-radius: 2px;
        border: 1px solid $c-white;
        color: $c-white;
        @include transition-ease;
        &:hover {
            background-color: $c-white;
            color: $c-black;
        }
        &:focus {
            outline: 0;
        }
    }
}

.btn-black-more {
    a {
        display: inline-block;
        padding: 7px 15px;
        border-radius: 2px;
        border: 1px solid $c-black;
        color: $c-black;
        @include transition-ease;
        &:hover {
            background-color: $c-black;
            color: $c-white;
        }
        &:focus {
            outline: 0;
        }
    }
    button {
        width: 100%;
        padding: 8px 10px;
        margin-bottom: 15px;
        font-weight: bold;
        line-height: 1.42;
        border-radius: 2px;
        border: 1px solid $c-black;
        color: $c-white;
        background-color: $c-black;
        @include transition-ease;
        &:hover {
            background-color: $c-white;
            color: $c-black;
        }
        &:focus {
            outline: 0;
        }
    }
}

.btn-black-addCart {
    display: block;
    padding: 10px;
    border-radius: 3px;
    border: 1px solid $c-black;
    background-color: $c-black;
    color: $c-white;
    outline: 0;
    @include transition-ease;
    &:hover {
        background-color: $c-white;
        color: $c-black;
    }
}

.btn-white-buyNow {
    display: block;
    padding: 10px;
    border-radius: 3px;
    border: 1px solid $c-black;
    background-color: $c-white;
    color: $c-black;
    outline: 0;
    @include transition-ease;
    &:hover {
        background-color: $c-black;
        color: $c-white;
    }
}

.collection-info {
    position: absolute;
    top: calc(50% - 75px);
    right: 30px;
    width: 100%;
    text-align: right;
    .title {
        margin-bottom: $mb-15;
        a {
            font-size: $font-size-secondary;
            color: $c-white;
            text-transform: uppercase;
        }
    }
    .desc {
        display: block;
        color: $c-white;
        margin-bottom: 20px;
        line-height: $line-height-secondary;
    }
}

.fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateY(-200px);
    background-color: white;
    z-index: 101;
}

.inView {
    transform: translateY(0px);
    animation: fixedNav 0.6s ease;
}

@keyframes fixedNav {
    0% {
        visibility: hidden;
        opacity: 0;
        transform: translateY(-200px);
    }
    100% {
        visibility: visible;
        opacity: 1;
        transform: translateY(0px);
    }
}

.product-item {
    padding: 10px 5px;
    margin-bottom: 10px;
    .image {
        text-align: center;
        margin-bottom: 7px;
        position: relative;
        .hoverSwap {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            bottom: 0;
            visibility: hidden;
            opacity: 0;
            @include transition-ease;
        }
        &::before {
            content: "";
            position: absolute;
            top: 5px;
            bottom: 5px;
            left: 5px;
            right: 5px;
            border-left: 1px solid $c-white;
            border-right: 1px solid $c-white;
            transform: scale(1, 0);
            opacity: 0;
            @include transition-ease;
        }
        &:hover::before {
            transform: scale(1);
            opacity: 1;
            z-index: 10;
        }
    }

    &:hover .hoverSwap {
        visibility: visible;
        opacity: 1;
    }

    .hover-underline {
        a {
            &::after {
                bottom: 0px;
            }
        }
    }

    .product-item__info {
        margin: 0px 5px;
        text-align: center;
        .title {
            display: inline-block;
            font-family: "Alegreya", serif;
            font-size: $fs-16;
            font-weight: 400;
            margin-bottom: 3px;
        }
        .sku-loop {
            display: block;
            margin-bottom: 3px;
        }
        .product-price {
            .current-price {
                font-weight: bold;
            }
        }
    }
}

.section-custom {
    margin: 30px 0;
    .section-title {
        text-transform: uppercase;
        margin-bottom: 12px;
        font-weight: bold;
        letter-spacing: 0px;
    }

    .wrapper {
        padding: 0 15px;
    }

    @include max-tablet {
        margin: 20px 0 !important;
    }
}

// Owl Carousel
.owl-carousel {
    .owl-dots {
        /* Khng nn  bt c display no  y, s b conflict v cc dots trong carousel*/
        .owl-dot {
            @include transition-ease;
            span {
                display: block;
                width: 12px;
                height: 12px;
                opacity: 0.5;
                @include transition-ease;
                background-color: #869791;
                &:hover {
                    opacity: 1 !important;
                    background-color: #869791 !important;
                }
            }
            &.active {
                span {
                    opacity: 1;
                    background-color: #869791; /* Active and on hover color */
                }
            }
            &:hover {
                color: #869791;
            }
            &:focus {
                border: none;
                outline: 0;
            }
        }
    }

    .owl-nav {
        [class*="owl-"]:hover {
            background: transparent;
            color: black;
        }

        .owl-prev,
        .owl-next {
            /* Khng nn  display: block  y*/
            position: absolute;
            top: calc(50% - 30px);
            margin: 0;
            a {
                i {
                    opacity: 0.2;
                    font-size: 45px;
                    @include transition-ease;
                    &:hover {
                        opacity: 0.5;
                    }
                }
            }
        }

        .owl-prev {
            left: 10px;
        }

        .owl-next {
            right: 10px;
        }
    }
}

/* TABLET */
@include max-tablet {
    .btn-white-more {
        a {
            font-size: $fs-13;
        }
    }
    .btn-black-more {
        a {
            font-size: $fs-13;
        }
    }
    .collection-info {
        top: calc(50% - 90px);
        .title {
            a {
                font-size: 27px;
            }
        }
        .desc {
            font-size: $fs-13;
            margin-bottom: $mb-15;
        }
    }
    .owl-carousel {
        .owl-nav {
            .owl-prev,
            .owl-next {
                /* Khng nn  display: block  y*/
                a {
                    i {
                        font-size: 30px;
                    }
                }
            }
            .owl-prev {
                left: 10px;
            }
            .owl-next {
                right: 10px;
            }
        }
    }
    .product-item {
        .product-item__info {
            .title {
                a {
                    font-size: $fs-15;
                }
            }
            .sku-loop {
                font-size: $fs-13;
            }
            .product-price {
                .current-price {
                    font-size: $fs-13;
                }
            }
        }
    }
}

/* MOBILE */
@include max-mobile {
    .wrapper {
        max-width: 1200px;
        margin: 0 auto;
        padding: 0 15px;
    }
    .btn-white-more {
        a {
            font-size: $fs-12;
        }
    }
    .btn-black-more {
        a {
            font-size: $fs-12;
        }
    }
    .collection-info {
        position: absolute;
        right: initial;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        text-align: center;
        .title {
            margin-bottom: $mb-30;
            a {
                text-transform: uppercase;
                font-size: $font-size-other;
                color: $c-white;
            }
        }
        .desc {
            display: none;
        }
    }
    .section-title {
        margin: 5px 0px;
        width: 100%;
    }
}
