/* === BEGIN MIXINS SCSS === */
@mixin transform($transforms) {
  -moz-transform: $transforms;
  -o-transform: $transforms;
  -ms-transform: $transforms;
  -webkit-transform: $transforms;
  transform: $transforms;
}

@mixin rotate($deg) {
  @include transform(rotate(#{$deg}deg));
}

@mixin scale($scale) {
  @include transform(scale($scale));
}

@mixin scaleX($scale) {
  @include transform(scaleX($scale));
}

@mixin scaleY($scale) {
  @include transform(scaleY($scale));
}

@mixin translate($x, $y) {
  @include transform(translate($x, $y));
}

@mixin translateX($x) {
  @include transform(translateX($x));
}

@mixin translateY($y) {
  @include transform(translateY($y));
}

@mixin skew($x, $y) {
  @include transform(skew(#{$x}deg, #{$y}deg));
}

@mixin transform-origin($origin) {
  moz-transform-origin: $origin;
  -o-transform-origin: $origin;
  -ms-transform-origin: $origin;
  -webkit-transform-origin: $origin;
  transform-origin: $origin;
}

@mixin transition($transition) {
  -webkit-transition: $transition;
  -moz-transition: $transition;
  -ms-transition: $transition;
  -o-transition: $transition;
  transition: $transition;
}

@mixin transition-delay($time) {
  transition-delay: $time;
  -moz-transition-delay: $time;
  -webkit-transition-delay: $time;
  -o-transition-delay: $time;
  -ms-transition-delay: $time;
}

@mixin box-shadow($top, $left, $blur, $color, $inset: false) {
  @if $inset {
    -webkit-box-shadow: inset $top $left $blur $color;
    -moz-box-shadow: inset $top $left $blur $color;
    box-shadow: inset $top $left $blur $color;
  } @else {
    -webkit-box-shadow: $top $left $blur $color;
    -moz-box-shadow: $top $left $blur $color;
    box-shadow: $top $left $blur $color;
  }
}

@mixin border-radius($radius) {
  -moz-border-radius: $radius;
  -webkit-border-radius: $radius;
  -ms-border-radius: $radius;
  -o-border-radius: $radius;
  border-radius: $radius;
}

@mixin opacity($value) {
  $IEValue: $value * 100;
  opacity: $value;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(opacity=" + $IEValue + ")";
  filter: alpha(opacity=$IEValue);
}

@mixin flexbox() {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
}

@mixin inline-flex {
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: -moz-inline-flex;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

/* flex-start, flex-end, center, space-around, space-between, initial, inherit */
@mixin justify-content($justify) {
  -webkit-box-pack: $justify;
  -moz-box-pack: $justify;
  -ms-flex-pack: $justify;
  -webkit-justify-content: $justify;
  justify-content: $justify;
}

/* flex-start, flex-end, center, space-around, space-between, initial, inherit */
@mixin align-items($align) {
  -webkit-align-items: $align;
  -moz-align-items: $align;
  -ms-align-items: $align;
  align-items: $align;
}

/* column, column-reverse, row, row-reverse */
@mixin flex-direction($direction) {
  -webkit-flex-direction: $direction;
  -moz-flex-direction: $direction;
  -ms-flex-direction: $direction;
  flex-direction: $direction;
}

/* wrap, no-wrap */
@mixin flex-wrap($wrap) {
  -webkit-flex-wrap: $wrap;
  -moz-flex-wrap: $wrap;
  -ms-flex-wrap: $wrap;
  flex-wrap: $wrap;
}

/* column, column-reverse, row, row-reverse; wrap, no-wrap */
@mixin flex-flow($flex-flow) {
  -webkit-flex-flow: $flex-flow;
  -moz-flex-flow: $flex-flow;
  -ms-flex-flow: $flex-flow;
  flex-flow: $flex-flow;
}

/* column, column-reverse, row, row-reverse; wrap, no-wrap */
@mixin calc($property, $expression) {
  #{$property}: -moz-calc(#{$expression});
  #{$property}: -webkit-calc(#{$expression});
  #{$property}: -ms-calc(#{$expression});
  #{$property}: calc(#{$expression});
}

/* === BUTTON === */
@mixin button--primary {
  background-color: $btn-primary--bg;
  color: $btn-primary--color;
  border-color: $btn-primary--border-color;
  font-weight: 500;
}

@mixin button--secondary {
  background-color: $btn-secondary--bg;
  color: $btn-secondary--color;
  border-color: $btn-secondary--border-color;
  font-weight: 400;
}

@mixin button--other {
  background-color: $btn-other--bg;
  color: $btn-other--color;
  border-color: $btn-other--border-color;
}

/* === BODY === */
@mixin flexCenter($direction) {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: $direction;
}
@mixin measure($font-size, $font-weight, $line-height, $letter-spacing) {
  font-size: $font-size;
  font-weight: $font-weight;
  line-height: $line-height;
  letter-spacing: $letter-spacing;
}
@mixin header-body {
  font-size: $font-size-header-primary;
  font-weight: 700;
  line-height: $line-height-top-header;
  letter-spacing: $letter-spacing-body-header;
}
@mixin desc-body {
  font-size: $font-size-secondary;
  font-weight: 400;
  line-height: $line-height-secondary-paragraph;
  letter-spacing: $letter-spacing-body-header;
}

/* === MIN WIDTH === */
@mixin smobile {
  @media (min-width: $s-mobile) {
    @content;
  }
}
@mixin mobile {
  @media (min-width: $mobile) {
    @content;
  }
}
@mixin tablet {
  @media (min-width: $tablet) {
    @content;
  }
}
@mixin desktop {
  @media (min-width: $desktop) {
    @content;
  }
}

/* === MAX WIDTH === */
@mixin max-smobile {
  @media only screen and (max-width: $s-mobile - 1) {
    @content;
  }
}
@mixin max-mobile {
  @media only screen and (max-width: $mobile - 1) {
    @content;
  }
}
@mixin max-tablet {
  @media only screen and (max-width: $tablet - 1) {
    @content;
  }
}
@mixin max-desktop {
  @media only screen and (max-width: $desktop - 1) {
    @content;
  }
}

/* === ONLY FOR IE === */
@mixin ie-only() {
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    @content;
  }
}

/* === TRANSITION FOR ALL === */
@mixin transition-ease {
  @include transition(all 0.5s ease);
}
@mixin transition-ease-slower {
  @include transition(all 0.3s ease);
}

@mixin limit-2-line {
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  display: -webkit-box;
  -webkit-box-orient: vertical;
}

@mixin custom-scrollbar-black {
  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.05);
    background-color: $c-white-f5f5f5;
  }

  &::-webkit-scrollbar {
    height: 4px;
    width: 4px;
    background: $c-grey-8b8b8b;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $c-black;
  }

  &::-webkit-scrollbar-thumb:horizontal {
    border-radius: 10px;
  }
}

/* === END MIXINS SCSS === */
