#home-banner2 {
    margin-bottom: $mb-30;
    .home-collection-wrapper {
        position: relative;
        > a {
            &:before {
                position: absolute;
                content: "";
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                background-color: rgba(0, 0, 0, 0.3);
            }
        }
        .collection-info {
            width: 100%;
            top: initial;
            bottom: 65px;
            left: 50%;
            transform: translateX(-50%);
            text-align: center;
        }
    }
}

/* TABLET */
@include max-tablet {
    #home-banner2 {
        margin-bottom: $mb-20;
        .home-collection-wrapper {
            .collection-info {
                top: 50%;
                bottom: initial;
                transform: translate(-50%, -50%);
            }
        }
    }
}
