/* === BEGIN HEADER SCSS === */
.header {
    padding: 0px;
    border-bottom: 1px solid $c-white-d4d4d4;

    .item-mobile,
    i.fa-plus {
        display: none;
    }

    .header-top {
        i {
            font-size: 18px;
        }

        .row {
            padding: 5px 20px;
            margin: 0px;
        }

        .hover-underline {
            .item:not(:nth-last-child(n + 6)) {
                a {
                    &:hover::after {
                        width: 0%;
                    }
                }
            }
        }

        .header-top__list {
            @include flexCenter(row);
            justify-content: flex-end;
            align-items: flex-end;
            margin: 12px 0px;

            .shopping-bag {
                display: block;
            }

            .item {
                position: relative;
                margin-left: 20px;

                .form-search-header {
                    position: absolute;
                    right: 80px;
                    top: 50%;
                    width: 350px;
                    opacity: 0;
                    visibility: hidden;
                    transform: translateY(-50%);
                    background: white;
                    @include transition-ease;

                    &.open {
                        opacity: 1;
                        visibility: visible;
                    }

                    .input-button {
                        @include flexCenter(row);
                        z-index: 99;

                        input,
                        #btnSearch {
                            height: 40px;
                            outline: 0;
                            border: 1px solid $c-white-e5e5e5;
                            &:focus {
                                border: 1px solid $c-grey-8b8b8b;
                            }
                        }

                        #btnSearch {
                            padding: 8px 10px;
                            border-left: 0;
                            i {
                                font-size: $fs-13;
                            }
                        }
                    }
                }
                .cart-count {
                    font-size: 12px;
                }
                .heart-count {
                    display: block;
                    font-size: 12px;
                    text-align: right;
                }
            }
        }
    }

    .header-bottom {
        .header-bottom__list {
            @include flexCenter(row);
            align-items: flex-end;
            .item {
                position: relative;
                padding: 10px 0px;
                margin: 0px 10px;
                .sub-items {
                    .sub-items__list {
                        position: absolute;
                        top: 35px;
                        width: 200px;
                        z-index: 99;
                        box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.3);
                        opacity: 0;
                        visibility: hidden;
                        transform: translateY(30%);
                        background-color: #fff;
                        .sub-item {
                            text-align: left;
                            padding: 10px;
                            a {
                                display: inline-block;
                            }
                        }
                    }
                }
                &:hover .sub-items__list {
                    opacity: 1;
                    visibility: visible;
                    transform: translateY(0%);
                    @include transition-ease;
                }
            }
        }
    }
}

#mobile-bottom-nav {
    display: none;
}

/* === Overlay === */
.overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    visibility: hidden;
    opacity: 0;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 102;
    @include transition-ease;
    &.open {
        visibility: visible;
        opacity: 1;
    }
}

@include max-mobile {
    #mobile-bottom-nav {
        .mobile-bottom-nav__main {
            .mobile-bottom-nav__item {
                i {
                    font-size: 20px;
                    margin-bottom: 3px;
                }
            }
        }
    }

    .header {
        i.fa-plus {
            display: block;
        }
        .header-top {
            .left {
                display: none;
            }
            .header-top__list {
                align-items: center;
                justify-content: space-between;
                a {
                    &:hover::after {
                        width: 0%;
                    }
                }
                .item {
                    display: none;
                    margin-left: 0px;
                }
                .item-mobile {
                    display: block;
                    &.shopping-bag {
                        a {
                            position: relative;
                            .brackets {
                                display: none;
                            }
                            .cart-count {
                                @include flexCenter(row);
                                font-size: 10px;
                                color: white;
                                position: absolute;
                                top: -10px;
                                right: -5px;
                                background-color: $c-black-181818;
                                padding: 2px 4px 2px;
                                border-radius: 50%;
                            }
                        }
                    }
                    .header-top__logo-mobile {
                        width: 100px;
                    }
                    i {
                        font-size: 22px;
                    }
                }
            }
        }
        .header-bottom {
            position: fixed;
            top: 0;
            bottom: 0;
            left: -300px;
            opacity: 0;
            visibility: hidden;
            z-index: 999;
            width: 300px;
            max-width: 95%;
            height: 100vh;
            @include transition-ease;
            &.open {
                left: 0;
                visibility: visible;
                opacity: 1;
            }
            .header-bottom__list {
                display: block;
                overflow-y: auto;
                overflow-x: hidden;
                height: 100%;
                background-color: $c-white-f6f6f6;
                z-index: 999;
                padding: 0px 15px 15px 15px;
                .item {
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;
                    width: 100%;
                    margin: 0px;
                    padding: 0px;
                    border-bottom: 1px solid #e9e9e9;
                    &.nav__form-search__mobile {
                        padding: 15px 0px;
                        #formSearchMobile {
                            display: flex;
                            width: 100%;
                            max-width: 100%;
                            input,
                            button {
                                min-height: 37px;
                                outline: none;
                                border: none;
                            }
                            input {
                                display: block;
                                border: 1px solid #e5e5e5;
                                width: 100%;
                                max-width: 100%;
                                font-size: $font-size-item-slide;
                                padding: 8px 10px;
                                border-radius: 4px 0px 0px 4px;
                            }
                            button {
                                border: 1px solid #e5e5e5;
                                border-radius: 0px 4px 4px 0px;
                                white-space: nowrap;
                                background-color: $c-black-111111;
                                color: white;
                                padding: 0px 12px;
                                border-left: 0;
                                i {
                                    font-size: 14px;
                                }
                            }
                        }
                    }
                    .sub-items {
                        width: 100%;
                        display: none;
                        .sub-items__list {
                            position: static;
                            top: initial;
                            width: auto;
                            z-index: 99;
                            box-shadow: none;
                            opacity: 1;
                            visibility: visible;
                            transform: translateY(0%);
                            background-color: $c-white-f6f6f6;
                            .sub-item {
                                text-align: left;
                                &:not(:last-child) {
                                    border-bottom: 1px solid $c-white-e9e9e9;
                                }
                                &:first-child {
                                    border-top: 1px solid $c-white-e9e9e9;
                                }
                                padding: 0px 0px 0px 15px;
                            }
                        }
                    }
                    &:hover .sub-items__list {
                        opacity: 1;
                        visibility: visible;
                        transform: translateY(0%);
                        transition: none;
                    }
                    &.logo-nav__mobile {
                        flex-wrap: nowrap;
                        position: relative;
                        height: 90px;
                    }
                    a {
                        display: block;
                        padding: 15px 0px;
                        width: 100%;
                        &:hover::after {
                            width: 0%;
                        }
                    }
                    i {
                        margin-left: auto;
                    }
                    .mobile-nav__toggle {
                        display: block;
                        padding: 15px;
                    }
                    .button.mobile-nav__close {
                        padding: 0px 14px 0px 32px;
                    }
                    .fa-angle-down {
                        display: none;
                    }
                }
            }
        }
    }
}

@include max-smobile {
    #mobile-bottom-nav {
        display: block;
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 98;
        .mobile-bottom-nav__main {
            @include flexCenter(row);
            justify-content: space-around;
            background-color: $c-black;
            .mobile-bottom-nav__item {
                @include flexCenter(row);
                position: relative;
                height: 67px;
                a {
                    @include flexCenter(column);
                    align-items: center;
                }
                i {
                    font-size: 18px;
                    margin-bottom: 3px;
                }
                span {
                    font-size: 12px;
                    &.cart-count {
                        position: absolute;
                        top: 5px;
                        right: 2px;
                        width: 20px;
                        height: 20px;
                        line-height: 20px;
                        color: $c-black;
                        text-align: center;
                        background-color: $c-white;
                        border-radius: 50%;
                    }
                }
                span,
                i {
                    color: $c-white;
                }
            }
        }
    }
}
