#cart-info {
    margin: 120px 0px;

    .table-cart {
        .table {
            width: 100%;
            max-width: 100%;
            border-bottom: 1px solid $c-black;
        }

        i {
            color: $c-grey-8b8b8b;
            transition: 0.35s;
            &:hover {
                color: $c-black;
            }
        }
    }

    thead {
        background: $c-black;
        color: $c-white;
        th {
            text-align: center;
        }
        tr,
        th {
            border: 1px solid $c-white;
            font-weight: 400;
            letter-spacing: 0.5px;
        }
    }

    tbody {
        input[type="number"] {
            border: 1px solid #dedede;
            height: 30px;
            max-width: 55px;
            padding: 0;
            &::-webkit-inner-spin-button,
            &::-webkit-outer-spin-button {
                height: 30px;
                opacity: 1;
            }
        }

        tr {
            a,
            span {
                font-size: $fs-16;
            }
        }

        td,
        th {
            text-align: left;
            vertical-align: middle;
            border-color: $c-black;
            .title-item {
                line-height: 18px;
                font-weight: 600;
                margin-bottom: 5px;
            }
        }

        .price-cart2 {
            font-weight: 400;
        }

        .image {
            a {
                max-width: 60%;
                margin: 0 auto;
            }
        }
    }

    .form-cart-continue,
    .btn-checkout {
        text-transform: uppercase;
        font-weight: 400;
        height: 50px;
        line-height: 50px;
        padding: 0px 30px;
        border: none;
        border-radius: 50px;
        background-color: $c-black;
        i {
            color: $c-white;
        }
    }

    .total-cart {
        border: 1px solid #eee;
        color: $c-grey-3e4848;
        .subtotal {
            border-bottom: 1px solid #eee;
            padding: 10px 25px;

            .total-price {
                font-weight: 600;
            }
        }
        .final-total {
            padding: 25px;
        }
    }
}

/* Responsive */
@include max-tablet {
    #cart-info {
        tbody {
            .image {
                a {
                    max-width: 80%;
                    margin: 0 auto;
                }
            }
        }
    }
}

@include max-mobile {
    #cart-info {
        .form-cart-continue {
            width: 100%;
        }

        thead {
            tr,
            th {
                font-size: 12px;
            }
        }

        .form-cart-continue,
        .btn-checkout {
            height: 45px;
            line-height: 45px;
            padding: 0px 30px;
        }

        tbody {
            tr {
                a,
                span {
                    font-size: 12px;
                }
            }
            td,
            th {
                .title-item {
                    font-size: 12px;
                    margin-bottom: 5px;
                }
            }
            .product {
                padding: 10px 0px;
                text-align: center;
                .image {
                    margin-bottom: 10px;
                }
            }
        }
    }
}
