#home-shop-map {
    display: block;
    position: relative;
    background-image: url("../assets/images/home-shop-map/1.webp");
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
    background-attachment: fixed;
    width: 100%;
    height: 300px;
    .collection-info {
        width: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
        .title {
            margin-bottom: $mb-30;
            a {
                font-size: 28px;
                letter-spacing: 4px;
            }
        }
    }
}

// TABLET
@include max-tablet {
    #home-shop-map {
        .collection-info {
            top: 50%;
            bottom: initial;
            transform: translate(-50%, -50%);
            .title {
                a {
                    font-size: $font-size-other;
                }
            }
        }
    }
}
